import React, { Component, Fragment } from 'react';
import { Col, Row, Label } from 'reactstrap';
import { Field } from 'redux-form';
import PropTypes from 'prop-types';
import uuid from 'uuid';

import {
  Accordion,
  Checkbox,
  Input,
  Modal,
  Card
} from '../UI';

import { COLORS } from '../../constants';

const list1 = [
  'Соціальні',
  'Освітні',
  'Медичні'
];
const list2 = [
  'Пошук, перегляд, друк картки послуги',
  'Пошук, перегляд, друк картки послуги',
  'Пошук, перегляд, друк картки послуги',
  'Пошук, перегляд, друк картки послуги',
  'Пошук, перегляд, друк картки послуги',
  'Пошук, перегляд, друк картки послуги',
  'Пошук, перегляд, друк картки послуги',
  'Пошук, перегляд, друк картки послуги',
  'Пошук, перегляд, друк картки послуги',
  'Пошук, перегляд, друк картки послуги',
  'Пошук, перегляд, друк картки послуги',
  'Пошук, перегляд, друк картки послуги',
  'Пошук, перегляд, друк картки послуги',
  'Пошук, перегляд, друк картки послуги',
  'Пошук, перегляд, друк картки послуги',
  'Пошук, перегляд, друк картки послуги',
  'Пошук, перегляд, друк картки послуги',
  'Пошук, перегляд, друк картки послуги',
];

const Records = () => (
  <Fragment>
    <Col>
      <Row>
        <Label style={{ color: COLORS.GREY, fontSize: 11 }}>
          ПРАВА НА  ВИДИ ПОСЛУГ
        </Label>
      </Row>
      {
        list1.map(cx => (
          <Row key={uuid.v4()}>
            <Checkbox
              labelStyle={{ fontSize: 11, fontWeight: 600 }}
              label={cx}
              onChange={() => {}}
            />
          </Row>
        ))
      }
      <hr />
      <Row>
        <Label style={{ color: COLORS.GREY, fontSize: 11 }}>
          ПРАВА НА ДІЇ
        </Label>
      </Row>
      {
        list2.map(cx => (
          <Row key={uuid.v4()}>
            <Checkbox
              labelStyle={{ fontSize: 10 }}
              label={cx}
              onChange={() => {}}
            />
          </Row>
        ))
      }
    </Col>
  </Fragment>
);

const dummy = [
  {
    label: 'Користувач',
    component: <Records />
  },
  {
    label: 'Реєстратор',
    component: <p>Реєстратор</p>
  },
  {
    label: 'Адміністратор',
    component: <p>Адміністратор</p>
  },
  {
    label: 'Адміністратор 2',
    component: <p>Адміністратор 2</p>
  }
];

class Roles extends Component {
  state = {
    isModalOpen: false
  }

  onToggleModal = () => {
    this.setState(prevState => ({ isModalOpen: !prevState.isModalOpen }));
  };

  onCreateRole = this.props.handleSubmit(values => {
    this.onToggleModal();
    this.props.createRoleRequested(values);
    this.props.reset('createRole');
  });

  renderCardHeader = () => (
    <Fragment>
      <strong>
        Ролі
      </strong>
      <div className="card-header-actions">
        <a className="card-header-action btn btn-minimize" style={{ color: COLORS.BLUE }} onClick={this.onToggleModal}>
          <i className="fa fa-plus" style={{ marginRight: 5 }} />
          <strong>
            Додати
          </strong>
        </a>
      </div>
    </Fragment>
  );

  render() {
    return (
      <Fragment>
        <Card header={this.renderCardHeader()} collapsable>
          <Accordion items={dummy} />
        </Card>
        <Modal
          successLabel="Зберегти"
          header="Створення нової ролі"
          isOpen={this.state.isModalOpen}
          body={
            <Field component={Input} label="Введіть назву" name="role" />
          }
          onClose={this.onToggleModal}
          onSuccess={this.onCreateRole}
        />
      </Fragment>
    );
  }
}

Roles.propTypes = {
  createRoleRequested: PropTypes.func.isRequired,
  reset: PropTypes.func.isRequired
};

export default Roles;



